.nav {
  width: 100%;
  height: 78px;
  background-color: #f2f1ed;
  display: flex;
  max-width: 1440px;
  justify-content: space-around;
  align-items: center;
  flex: 1 0 0;
}
.nav__links {
  display: flex;
  align-items: flex-start;
  gap: 48px;
  align-self: stretch;
  place-items: center;
}
.nav__links__list:hover .nav__links__list__sub {
  opacity: 1;
  visibility: visible;
  transition: visibility 0s ease 0s, opacity 0.5s ease 0s;
}
.nav__links__list__sub {
  position: absolute;
  width: 100%;
  min-width: 14rem;
  top: 34px;
  left: 0;
  opacity: 0;
  visibility: hidden;
  -webkit-transition: visibility 0s ease 0.6s, opacity 0.5s ease 0s;
  transition: visibility 0s ease 0.6s, opacity 0.5s ease 0s;
  padding: 20px;
  background-color: #ffffff;
  border-radius: 4px;
  -webkit-box-shadow: 0 102px 98px rgba(0, 0, 0, 0.15);
  box-shadow: 0 102px 98px rgba(0, 0, 0, 0.15);
  z-index: 2;
}
.nav__links__list__sub--list {
  padding: 6px 0;
}
.nav__links__list__sub--list .nav-links {
  display: flex;
  color: #1a2f35;
}
.nav__links__list__sub--mobile {
  width: 100%;
  position: relative;
  margin: 0;
  padding: 0;
  overflow: hidden;
  -webkit-box-shadow: inset 0 -7px 12px rgba(0, 0, 0, 0.35) !important;
  box-shadow: inset 0 -7px 12px rgba(0, 0, 0, 0.35) !important;
  border-top: 1px solid #ffffff;
}
.nav__links__wrapper {
  padding-left: 30px;
}
.nav__dropdown {
  display: none;
  position: absolute;
  background-color: #1a2f35;
  text-align: center;
  padding: 40px 42px 0;
  height: 100vh;
  width: 100%;
  grid-auto-rows: minmax(min-content, 0);
}
.nav__dropdown .nav__links__list {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.nav__dropdown .nav__links__list:last-of-type .nav__items:last-of-type {
  border-bottom: none;
}
.nav__dropdown .nav__items {
  padding: 20px 0;
}
.nav__items {
  color: #1d1c1d;
  font-family: "Inter", sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
  text-transform: uppercase;
  position: relative;
}
@media (max-width: 1200px) {
  .nav__items {
    font-size: 1.125em;
    margin-right: 0;
  }
}
@media (max-width: 479px) {
  .nav__items {
    border-bottom: 1px solid #ffffff;
    font-size: 1.125em;
    margin-right: 0;
  }
}
.nav__items-dropdown {
  font-family: "DJ5 Escrow Light Display", sans-serif;
}
.nav__items:hover {
  color: #b9a569;
}

.btn--header {
  position: absolute;
  right: 150px;
  top: 12px;
  margin: 10px;
  background-color: #b9a569 !important;
}
.btn--header .btn--primary__link {
