.brand__container {
  position: absolute;
  left: 5rem;
  top: 10px;
  overflow: hidden;
  z-index: 2;
  display: flex;
  transition: all 0.2s ease-out;
}
@media (max-width: 479px) {
  .brand__container {
    left: 0;
    right: 0;
    margin: auto;
    width: 200px;
  }
}
.brand__container__fig {
  width: 270px;
  min-height: 70px;
  margin: 2px 0;
  background-color: #1a2f35;
  padding: 10px;
  transition: all 0.2s ease-in-out;
}
@media (max-width: 479px) {
  .brand__container__fig {
    padding: 10px;
  }
}
.brand__container__fig--a {
  display: flex;
  width: 100%;
  height: 100%;
}
.brand__container--fixed {
  min-height: 60px;
}
.brand__container--fixed .brand__container__fig {
  min-height: 60px;
  padding: 0;
  box-shadow: none;
  margin: 0;
}

.brand__logo {
  max-width: 35px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  padding: 5px 0;
}
.brand__logo--no-scroll {
  display: block;
  animation-name: fade-out;
  animation-timing-function: ease-in-out;
  animation-duration: 0.6s;
}
.brand__logo--scroll {
  display: none;
  padding: 5px 0;
  max-width: 177px;
  animation-name: fade-out;
  animation-timing-function: ease-in-out;
  animation-duration: 0.5s;
}

.fixed-position {
  position: fixed;
}

@keyframes fade {
  0% {
    opacity: 1;
  }
  25% {
    opacity: 1;
  }
  75% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}
@keyframes fade-out {
  0% {
    opacity: 0;
  }
  25% {
    opacity: 0;
  }
  75% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}
@media (min-width: 639px) {
  .past-event {
    display: none !important;
  }
}

