.fadeIn {
  animation: slideIn 0.5s ease-out forwards;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(15%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.move-up {
  opacity: 0;
  -webkit-transform: translateY(15%);
  transform: translateY(15%);
}

.calendar {
  position: absolute;
  right: 90px;
  top: 0;
}
@media (max-width: 767px) {
  .calendar {
    position: relative;
    right: 0;
    top: 0;
    left: 0;
  }
}
.calendar__title {
  display: flex;
  position: relative;
  padding: 15px 10px;
  z-index: 3;
}
.calendar__title:hover {
  cursor: pointer;
}
.calendar__title--icon {
  background-image: url(../images/calendar-plus.svg);
  background-size: 70%;
  width: 25px;
  height: 25px;
  background-repeat: no-repeat;
}
.calendar__title--text {
  font-family: "Retina Book";
  letter-spacing: 0.9px;
  font-size: 0.9em;
  color: #000000;
}
.calendar__body {
  position: absolute;
  z-index: 4;
  top: 45px;
}
.calendar__body__list {
  display: flex;
  flex-direction: column;
  justify-content: start;
  text-align: left;
  padding: 12px 0;
  background-color: #ffffff;
  border: 1px solid #abadb0;
  border-radius: 0.25rem;
  min-width: 144px;
}
.calendar__body__list--item {
  color: #000000;
  padding: 5px 20px;
}
.calendar__body__list--item:hover {
  background-color: #f1f1f1;
}

.plus {
  position: absolute;
  right: 30px;
  width: 16px;
  height: 12px;
  margin-top: -6px;
  transition: all 250ms;
  top: 26px;
  cursor: pointer;
}
.plus::after {
  position: absolute;
  top: -2px;
  right: 3px;
  display: block;
  width: 8px;
  height: 8px;
  content: "";
  transform: rotate(-45deg);
  border-bottom: 2px solid #000000;
  border-left: 2px solid #000000;
}
@media (max-width: 767px) {
  .plus {
    position: relative;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    margin: 0 auto;
  }
}

.plus-active {
  transition: all 250ms;
  transform: rotate(180deg);
}